<template>
  <div>
    <!-- MIN FILE <2MB OR MAX FILE >2MB -->
    <Upload
        multiple
        type="drag"
        @on-success="getMediaList"
        :before-upload="handleUpload"
        :format="['jpg','png']"
        :max-size="2000"
        :action="service_upload">
      <div style="padding: 20px 0">
        <Icon type="ios-cloud-upload" size="52" ></Icon>
        <p>Click or drag files here to upload</p>
      </div>
    </Upload>

    <div v-if="file !== null">
      Upload file: {{ file.name }} {{file.type}}
      <Button type="text" @click="onMediaUpdata" :loading="loadingStatus">{{ loadingStatus ? 'Uploading' : 'Click to upload' }}</Button>
    </div>

    <Card :padding="10">
      <Row>
        <Col flex="1">
          {{ media.data.todayFileNumber || 0 }}ä»½æ–‡ä»¶
        </Col>
        <Col span="8">
          å®¹é‡
          <Progress :percent="media.data.usedStorageQuota || 0" :max="media.data.totalStorageQuota || 0" />
        </Col>
      </Row>
    </Card>

    <div>
      <Card v-for="(i, index) in media.list" :key="index">

      </Card>
    </div>
  </div>
</template>

<script>
import {api, http, http_token} from "../../assets/js";

export default {
  data() {
    return {
      file: { name: '' },
      loadingStatus: false,
      service_upload: api['service_upload'],
      media: {
        data: {},
        list: [],
      }
    }
  },
  created() {
    this.http = http_token.call(this);
    this.getMedia();
    this.getMediaList();
  },
  methods: {
    /**
     * æŸ¥è¯¢åª’ä½“ä¿¡æ¯
     */
    getMedia() {
      this.http.get(api["service_myStorageQuota"], {}).then(res => {
        const d = res.data;
        if (d.success === 1) {
          this.media.data = d;
        }
      }).finally(() => {
      });
    },
    /**
     * æŸ¥è¯¢åª’ä½“åˆ—è¡¨
     */
    getMediaList() {
      this.http.get(api["service_myFiles"], {
        par: {
          limit: 0,
          skip: 0,
        }
      }).then(res => {
        const d = res.data;
        if (d.success === 1) {
          this.media.list = d.data;
        }
      }).finally(() => {
      });
    },
    /**
     * æŸ¥è¯¢æ–‡ä»¶è¯¦æƒ…
     */
    queryMediaDetail () {
      this.http.get(api["service_file"], {}).then(res => {
        const d = res.data;
        if (d.success === 1) {
          this.media.list = d;
        }
      }).finally(() => {
      });
    },
    /**
     * ä¸Šä¼ 
     */
    onMediaUpdata () {
      if (!this.file) return;

      this.loadingStatus = true;

      this.http.put(api["service_upload"], {
        body: this.file,
        headers: {
          'Content-Size': this.file.size,
          'Content-Type': this.file.type,
        }
      }).then(res => {
        const d = res.data;
        if (d.success === 1) {
          this.file = null;
          this.loadingStatus = false;
          this.$Message.success('Success')
        }
      }).finally(() => {
      });
    },
    handleUpload (file) {
      this.file = file;
      console.log(file)
      return false;
    }
  }
}
</script>